
h1 {
  font-size: 2em;
  margin: .67em 0
}


.App {
  margin: 8px
}

.product {
  display: flex;
  flex-direction: column;
  height: 100%
}

.product--title {
  overflow: hidden;
  padding-top: 8px;
  text-overflow: ellipsis;
  white-space: nowrap
}

.product--image {
  margin-bottom: auto;
  margin-top: auto;
  max-height: 300px;
  object-fit: contain;
  width: 100%
}

.categories {
  display: flex
}

.categories--item {
  background-color: #efeff0;
  cursor: pointer;
  font-weight: 700;
  margin: 2px;
  padding: 12px
}

.categories--item:hover {
  background-color: #d6d6d8
}

.categories--item-selected {
  background-color: #bdbdc1
}

.products {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-padding-start: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin-block-end:0;margin-block-start:0;padding-inline-start:0}

.products--item {
  margin: 8px;
  width: 100%
}

@media (min-width: 600px) {
  .products--item {
      width:calc(50% - 16px)
  }
}

@media (min-width: 1000px) {
  .products--item {
      width:calc(33% - 16px)
  }
}

@media (min-width: 1600px) {
  .products--item {
      width:calc(25% - 16px)
  }
}

